@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Cairo+Play&family=Poppins:wght@500&family=Roboto:wght@300;400&family=Satisfy&display=swap');
@layer base {
  html {
    font-family: "Asterone-Regular";
  }

  @font-face {
    font-family: AsteroneOutline-Regular;
    src: url(./assets/fonts/Asterone/AsteroneOutline-Regular.otf);
  }
  @font-face {
    font-family: AsteroneOutline-Bold;
    src: url(./assets/fonts/Asterone/AsteroneOutline-Bold.otf);
  }

  @font-face {
    font-family: Asterone-Regular;
    src: url(./assets/fonts/Asterone/Asterone-Regular.otf);
  }
  @font-face {
    font-family: Asterone-Bold;
    src: url(./assets/fonts/Asterone/Asterone-Bold.otf);
  }
}


.edit-cursor:hover {
  cursor: url("./assets/cursor/box-hover.svg") 128 128, pointer;
}

.edit-cursor {
  cursor: url("./assets/cursor/box-non-edit-hover.svg") 128 128, pointer;
}

.non-edit-cursor {
  cursor: url("./assets/cursor/box-non-edit-hover.svg") 128 128, pointer;
}

