.nav-background {
  background: url("../../../assets/background/nav-background.svg");
  
}

.link-underline {
  
  border-bottom-width: 0;
  background-position: 0 100%;
   background-size: 200% 200%;
  background-repeat: no-repeat;
  text-shadow: 0px 0px 0px rgba(0, 0, 0, 0);
  border-radius: 1px;
  font-size: 1rem;

  transition: background-size 0.2s ease;
}

.link-underline:hover {
  background-size: 100% 100%;
  transition: background-size 0.2s ease-out;
}
 
.link-underline:hover > .link-text > span:nth-child(1) {
  text-shadow: 4px 4px 5px #f3efe0;
}


.nav-link-active {
  background-size: 100% 100%;
  transition: background-size 0.2s ease-out;
  border-radius: 3px;

}
.nav-link-active > * {
  transition:  0.3s;
}

.nav-link-image-1.nav-link-active > span > span:nth-child(2) {
  color: #59C1BD!important;
}

.nav-link-image-2.nav-link-active > span > span:nth-child(2) {
  color: #a350cc!important;
}

.nav-link-image-3.nav-link-active > span > span:nth-child(2) {
  color: #fc994e!important;
}

.nav-link-image-4.nav-link-active > span > span:nth-child(2) {
  color: red!important;
}




/* text animation css */

.link-text{
  width: 100%;
  height:100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.link-text span:nth-child(1) {
  position: absolute;
}

.link-text span:nth-child(2) {
  animation: animate 4s ease-in-out infinite;
}



@keyframes animate {
  0%{
    clip-path: polygon(
      0% 45%,
      16% 44%,
      33% 50%,
      54% 60%,
      70% 61%,
      84% 59%,
      100% 52%,
      100% 100%,
      0% 100%
    );
  }
  
  100% {
    clip-path: polygon(
      0% 45%,
      16% 44%,
      33% 50%,
      54% 60%,
      70% 61%,
      84% 59%,
      100% 52%,
      100% 100%,
      0% 100%
    );
  }

  50% {
    clip-path: polygon(
      0% 60%,
      15% 65%,
      34% 66%,
      51% 62%,
      67% 50%,
      84% 45%,
      100% 46%,
      100% 100%,
      0% 100%
    );
  }
}

/* Animating Show Navbar - Mobile */
.mobile-menu {
  position: fixed;
  z-index: 100000;
  /* color: #171717; */
  background-image: url("../../../assets/patterns/navbar-mobile.svg");
  top: -800px!important;
  width: 98vw;
  min-height: 60vh;
  transition: 1s;
  display: flex;
  justify-items: center;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-top: 40px;
  border-radius: 10px;
  margin: 1vw;
}

.mobile-menu-animate{
  top: 100px!important;
  transition: 0.6s;

}

.rotate-close{
  color: White;
  transform: rotate(0deg);
  transition: 1s;
}
.rotate-close:hover{
  color: #f44336;
  transform: rotate(180deg);
  transition: 1s;
}

@keyframes rotateOn {
  to {
            transform: rotate(360deg);
  }
  from{
    transform: rotate(0deg);

  }
}




/* Menu CSS */
.custom-menu{
  font-family: 'Roboto', sans-serif;
  text-align: center;
}

 
.custom-menu > * {
  transition: 1s;
}

.custom-menu > button:nth-child(1):hover{
  background-color: #59C1BD;
  color: white;
  transition: 0.2s;
}
.custom-menu > button:nth-child(3):hover{
  background-color: #fc994e;
  color: white;
  transition: 0.2s;

}
.custom-menu > button:nth-child(5):hover{
  background-color: #EB455F;
  color: white;
  transition: 0.2s;

}