.skill-component {
  border: 3px solid #ffffff;
  border-radius: 0.5rem;
  padding: 1rem 1.5rem;
}

.skill-card {
  border: 1px solid #ffffff00;
}

.skill-card:hover {
  border: 1px solid #ffffff;
  animation: skillcardscalein 0.3s both linear;
  z-index: 10000;
  
}

@keyframes skillcardscalein {
  from {
    scale: 1;
    opacity: 0.7;
  }
  to {
    scale: 1.2;
    opacity: 1;
  }
}

/* Star Component */

.stars {
  display: flex;
}

.star {
  padding: 0.2rem;
}
.star:hover {
  animation: rotation 0.1s infinite linear;
}
.star-complete {
  color: #97dece;
}

.star-incomplete {
  color: #fffbeb;
}

@keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}

