.settings {
  /* opacity: 0; */
  top: -100%;
  left: 50%;
z-index: 1000;
  transform: translate(-50%, -50%);
  min-width: 80%;
  font-family: 'Roboto', sans-serif;
  transition: 1s;
}

.show-settings {
  opacity: 1;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: 1s;
}

.settings-item{
    position: relative;
    left: 0px;
    opacity: 1;
    transition: 0.2s;

}
.settings-item-hide{
    left: -1070px;
    opacity: 0;
    transition: 1s;

}

