.control-buttons {
  display: flex;
  justify-content: space-between;
  position: relative;
  width: 100%;
  margin-bottom: 1rem;
}
.control-buttons .next {
  width: 6rem;
}

.control-buttons .prev {
  width: 6rem;
}

.button {
  width: 100%;
  border-radius: 6px;
  color: #fff;
  font-size: 15px;
  height: 36px;
  transition: 0.3s;
  cursor: pointer;
}

.nextcomp {
  background-color: #ffc107;
}
.nextcomp:hover {
  color: #ffc107;
  background-color: #fff8e4;
}

.prevcomp {
  background-color: #2196f3;
}
.prevcomp:hover {
  color: #2196f3;
  background-color: #ddedfa;
}

.inside-section {
  transition: opacity 1s;
}

.active {
  opacity: 0;
  transition: opacity 1s;
}

.about-loading {
  text-align: center;
  opacity: 0;
  transition: opacity 1s;
  position: relative;
  top:30vh;
  display: flex;
  justify-content: center;
}
.about-loading-active {
  text-align: center;
  opacity: 1;
  transition: opacity 1s;

}
